fica que es necesario emplear una cantidad adecuada ligada con la disponibilidad y reserva que se debe mantener en las fuentes hídricas con la calidad idónea para ser aprovechada; es decir, con propiedades fisicoquímicas (pH, turbidez, temperatura, etc.) en condiciones normales para el consumo de los seres vivos. Sostener las anteriores condiciones para garantizar la disponibilidad futura del agua requiere de planes de acción inmediatos que busquen una óptima gestión pensando en la demanda y la oferta actual y con proyección a las necesidades del futuro.")])])])]),_c('div',{staticClass:"crd crd--avatarHorizontal crd--last py-4 tarjeta3-conectadas-tema2"},[_c('div',{staticClass:"row align-items-center"},[_c('div',{staticClass:"col-auto"},[_c('figure',[_c('img',{staticClass:"img100 figura1-tarjetas-conectadas-tema2",attrs:{"src":require("@/assets/curso/temas/imagen-solucion-tema2.1.png"),"alt":"alt"}})])]),_c('div',{staticClass:"col"},[_c('h5',{staticClass:"texto-tarjetas-conectadas-tema2"},[_vm._v("Solución")]),_c('p',{staticClass:"texto-tarjetas-conectadas-tema2"},[_vm._v("Los planes de gestión deben iniciar por campañas de sensibilización de la población, en las cuales se resalte la importancia de cuidar el agua desde casa, en tareas sencillas del hogar como el lavado, el uso personal como la ducha y en actividades cotidianas que aporten a la preservación y cuidado. De igual forma es necesario que las actividades de tipo industrial reorganicen el consumo, ya sea para tareas de riego o simplemente para la elaboración de productos; por ello es importante considerar la reutilización de aguas lluvia, pensar en programas de agricultura sostenible con la recaptación de aguas y encaminarse en procesos investigativos que fomenten el acceso al agua potable para toda la población.")])])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-around align-items-center",attrs:{"data-aos":"flip-rig